body > div > a {
    text-decoration: none;
  }
  
  .container-login{
    /* background: #1abc9c; */
    background-image: url('../../images/pexels-pixabay-262353.jpg');
    background-size: cover;
    background-position-x: center;
    height: 100vh;
    /* max-width: 440px; */
    /* padding: 0 20px; */
    /* margin: 170px auto; */
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 8%;
  }
  .wrapper-login{
    width: 400px;
    height: 380px;
    padding-top: 18px;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0px 4px 10px 1px rgba(0,0,0,0.1);
  }
  .wrapper-login .title{
    height: 90px;
    /* background: #33343c; */
    margin-top: 15px;
    border-radius: 5px 5px 0 0;
    color: #fff;
    /* font-size: 30px; */
    /* font-weight: 600; */
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .wrapper-login form{
    padding: 30px 25px 25px 25px;
  }
  .wrapper-login form .row{
    height: 45px;
    margin-bottom: 10px;
    position: relative;
    margin-left: 20px;
    margin-right: 20px;
  }
  .wrapper-login form .row input{
    height: 100%;
    width: 100%;
    outline: none;
    padding-left: 60px;
    border-radius: 5px;
    border: 1px solid lightgrey;
    /* font-size: 16px; */
    transition: all 0.3s ease;
  }
  form .row input:focus{
    border-color: #01a9e6;
    box-shadow: inset 0px 0px 2px 2px rgba(26,188,156,0.25);
  }
  form .row input::placeholder{
    color: #999;
  }
  .wrapper-login form .row i{
    position: absolute;
    width: 47px;
    height: 100%;
    color: #fff;
    /* font-size: 18px; */
    background: #01a9e6;
    border: 1px solid #01a9e6;
    border-radius: 5px 0 0 5px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .wrapper-login form .pass{
    margin: -8px 0 20px 0;
  }
  .wrapper-login form .pass a{
    color: #01a9e6;
    /* font-size: 17px; */
    text-decoration: none;
  }
  .wrapper-login form .pass a:hover{
    text-decoration: underline;
  }
  .wrapper-login form .button input{
    color: #fff;
    /* font-size: 20px; */
    /* font-weight: 500; */
    padding-left: 0px;
    background: #01a9e6;
    border: 1px solid #01a9e6;
    cursor: pointer;
  }
  form .button input:hover{
    background: #0182b1;
    text-decoration: none;
  }
  .wrapper-login form .signup-link{
    text-align: center;
    margin-top: 15px;
    /* font-size: 17px; */
  }
  .wrapper-login form .signup-link a{
    color: #16a085;
    text-decoration: none;
  }
  form .signup-link a:hover{
    text-decoration: none;
  }
  .button{
    margin-top: 40px;
  }